.addBtn-main {
	width: 95%;
	margin: auto;
	margin-bottom: 20px;
}

.add-btn-inp {
	padding:10px;

	height: 20px;
	background: #79f073;
	width: 43%;

	border-radius: 95px;
	border: none;

	font-size: 24px;
	color: white;
	text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.25);
}

.add-btn-inp-main {
	display: flex;
	justify-content: space-around;

	margin-bottom: 10px;
	width: 100%;
}

.add-btn-inp:focus {
	outline: none;
}

.addBtn-main-btn {
	display: flex;
	justify-content: center;
	align-items: center;

	height: 60px;

	background: #92FE78;
	box-shadow: 4px 4px 0px rgba(0, 0, 0, 0.25);
	border-radius: 95px;
}

.addBtn-m-text {
	font-size: 48px;
	color: white;
	text-shadow: 3px 3px 0px rgba(0, 0, 0, 0.25);
}

.addBtn-desc {
	font-size: 18px;
}