@import url(https://fonts.googleapis.com/css?family=Roboto:500);
* {
	margin: 0;
	padding: 0;
}
.google-btn {
  width: 184px;
  height: 42px;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn .google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-btn .google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: 11px;
  width: 18px;
  height: 18px;
}
.google-btn .btn-text {
  float: right;
  margin: 13px 11px 0 0;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
  background: #1669F2;
}
.log-main {
	height: 50vh;
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-direction: column;
}

.log-whiteThingy {
	margin-top:20%;
	text-align: center;
}
.addBtn-main {
	width: 95%;
	margin: auto;
	margin-bottom: 20px;
}

.add-btn-inp {
	padding:10px;

	height: 20px;
	background: #79f073;
	width: 43%;

	border-radius: 95px;
	border: none;

	font-size: 24px;
	color: white;
	text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.25);
}

.add-btn-inp-main {
	display: flex;
	justify-content: space-around;

	margin-bottom: 10px;
	width: 100%;
}

.add-btn-inp:focus {
	outline: none;
}

.addBtn-main-btn {
	display: flex;
	justify-content: center;
	align-items: center;

	height: 60px;

	background: #92FE78;
	box-shadow: 4px 4px 0px rgba(0, 0, 0, 0.25);
	border-radius: 95px;
}

.addBtn-m-text {
	font-size: 48px;
	color: white;
	text-shadow: 3px 3px 0px rgba(0, 0, 0, 0.25);
}

.addBtn-desc {
	font-size: 18px;
}
* {
	font-family: 'Roboto', sans-serif;
}

.toDo-big {
	width: 95%;
	margin: auto;
}

.toDo-main {
	height: auto;

	display: flex;

	justify-content: space-between;

	margin-top: 20px;
	transition: 0.5s;
}

.toDo-image {
	height: 32px;

	-webkit-filter: drop-shadow(3px 3px 0px rgba(0, 0, 0, 0.25));

	        filter: drop-shadow(3px 3px 0px rgba(0, 0, 0, 0.25));
}

.toDo-text {
	font-size: 18px;
	font-weight: 400;

	transition: 0.5s;
}

.open {
	height: auto;
	margin-bottom: 0px;
	background: #eee;
	padding-bottom: 20px;
	align-items: flex-start;

	box-shadow: 4px 4px 0.5px rgba(0, 0, 0, 0.25);
}

.closed {
  height: 5vh;
  transition: 0.5s ease-out;
  margin-bottom: 10px;
  align-items: center;
  transition: 0.5s;
}

.toDo-popUp {
	padding-top: 15px;
	color:#777777;
	font-size: 16px;

	display: flex;
	width:100%;
	flex-direction: column;
	justify-content: center;
}

.toDo-t-o {
	color: #4A4A4A;
}

.toDo-t-c {
	color: #777777;
}

.toDo-p-btn {
	width: 100px;
	height: 40px;

	margin-top: 10px;
	border: 2px solid green;

	color:green;

	transition: 0.5s;

	margin-right: 20px;
}

.toDo-p-btn:hover {
	outline: none;
	color: white;
	background: green;
}

.toDo-p-btn:focus {
	outline: none;
}
.main-main {
	height: 89vh;
	margin-top: 5px;
	flex: 1 1;

	display: flex;
	flex-direction: column;
	justify-content: space-between;

	overflow: hidden;
}

.main-scrollable {
	overflow-y: scroll;
	scroll-behavior: smooth;
}
* {
	font-family: 'Roboto', sans-serif;
}

