	align-items: center;

	color: #4A4A4A;
	font-size: 22px;
	text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.25);
}

.h-l-s-top {
	color:#4A4A4A;
}

.h-l-s-bottom {
	color:#7D7D7D;
}
.main {
	width:750;
	min-height:100vh;
	background: #FFF;

	width: 90%;
	margin: auto;

	display: flex;
	flex-direction: column;

	overflow-y: hidden;
	overflow-x: hidden;
}
