* {
	font-family: 'Roboto', sans-serif;
}

.toDo-big {
	width: 95%;
	margin: auto;
}

.toDo-main {
	height: auto;

	display: flex;

	justify-content: space-between;

	margin-top: 20px;
	transition: 0.5s;
}

.toDo-image {
	height: 32px;

	filter: drop-shadow(3px 3px 0px rgba(0, 0, 0, 0.25));
}

.toDo-text {
	font-size: 18px;
	font-weight: 400;

	transition: 0.5s;
}

.open {
	height: auto;
	margin-bottom: 0px;
	background: #eee;
	padding-bottom: 20px;
	align-items: flex-start;

	box-shadow: 4px 4px 0.5px rgba(0, 0, 0, 0.25);
}

.closed {
  height: 5vh;
  transition: 0.5s ease-out;
  margin-bottom: 10px;
  align-items: center;
  transition: 0.5s;
}

.toDo-popUp {
	padding-top: 15px;
	color:#777777;
	font-size: 16px;

	display: flex;
	width:100%;
	flex-direction: column;
	justify-content: center;
}

.toDo-t-o {
	color: #4A4A4A;
}

.toDo-t-c {
	color: #777777;
}

.toDo-p-btn {
	width: 100px;
	height: 40px;

	margin-top: 10px;
	border: 2px solid green;

	color:green;

	transition: 0.5s;

	margin-right: 20px;
}

.toDo-p-btn:hover {
	outline: none;
	color: white;
	background: green;
}

.toDo-p-btn:focus {
	outline: none;
}