* {
	font-family: 'Roboto', sans-serif;
}

.header-main {
	width: 95%;
	margin: auto;
	margin-top: 17px;

	display: flex;
	justify-content: space-between;
}

.header-left {
	display: flex;
}

.header-bigNum {
	font-size: 48px;
	display: flex;
	justify-content: center;
	align-items: center;

	color: #2E2E2E;

	text-shadow: 3px 3px 0px rgba(0, 0, 0, 0.25);
}

.header-left-small {
	margin-left: 5px;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.25);
}

.header-right {
	display: flex;
	justify-content: center;
	align-items: center;

	color: #4A4A4A;
	font-size: 22px;
	text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.25);
}

.h-l-s-top {
	color:#4A4A4A;
}

.h-l-s-bottom {
	color:#7D7D7D;
}